<template>
  <div class="personal">
    <head-top :go-back="true"></head-top>
    <div class="container">
      <div class="avatar">
        <div class="top">个人信息</div>
        <div class="bottom" @click="openAvatar">
          <span v-if="isLogin" :style="{backgroundImage: 'url(' + userInfo.headPic + ')'}"></span>
          <em>{{userInfo.nickName}}</em>
        </div>
      </div>
      <div class="warp">
        <ul class="list">
          <li @click="itemHandle(1)">
            <span>{{ $t("Nickname") }}</span>
            <strong>{{userInfo.nickName}}</strong>
          </li>
          <li @click="itemHandle(2)">
            <span>{{ $t("cellPhoneNumber") }}</span>
            <strong v-if="bindPhone">{{ $t("Bound") }}</strong>
            <strong v-else>{{ $t("Unbound") }}</strong>
          </li>
          <li @click="itemHandle(3)">
            <span>{{ $t("WeChat") }}</span>
            <em v-if="bindWechat">{{ $t("Bound") }}</em>
            <strong v-else>{{ $t("Unbound") }}</strong>
          </li>
          <li @click="itemHandle(4)">
            <span>{{ $t("Alipay") }}</span>
            <strong v-if="bindAlipay">{{ $t("Bound") }}</strong>
            <strong v-else>{{ $t("Unbound") }}</strong>
          </li>
        </ul>
        <button class="button" @click="logout">
          <img src="../../assets/sidebar_images/sign_out@3x.png" alt="">
          <span>{{ $t("Logout") }}</span>
        </button>
      </div>
      <!-- <div class="avatar" @click="openAvatar">
        <div class="left">{{ $t("Avatar") }}</div>
        <div class="right">
          <span v-if="isLogin" :style="{backgroundImage: 'url(' + userInfo.headPic + ')'}"></span>
          <span v-else></span>
        </div>
      </div>
      <div class="content">
        <router-link to="/rename" class="box bomPixel">
          <div class="left">{{ $t("Nickname") }}</div>
          <div class="right">
            <span>{{userInfo.nickName}}</span>
            <img src="../../assets/global_images/next.png" alt="">
          </div>
        </router-link>
        <router-link to="/personal" class="box bomPixel">
          <div class="left">{{ $t("WeChat") }}</div>
          <div class="right bind">
            <span>{{ $t("Unbound") }}</span>
          </div>
        </router-link>
        <router-link to="/personal" class="box">
          <div class="left">{{ $t("Alipay") }}</div>
          <div class="right nobind">
            <span>{{ $t("Unbound") }}</span><img src="../../assets/global_images/next.png" alt="">
          </div>
        </router-link>
      </div>
      <div class="logout" @click="logout">
        <strong>
          <img src="../../assets/personal_images/back.png" alt="">
          <span>{{ $t("Logout") }}</span>
        </strong>
      </div> -->
    </div>

    <!-- logout弹窗 -->
    <message-box  :id="6"
                  icon="logout"
                  :title="$t('Logout')"
                  :describe="$t('exitAccount')"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>
    <!-- 解绑 -->
    <message-box  :id="7"
                  :title="$t(bindType)"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>
    <!-- 操作表 -->

    <action-sheet :id="2"
                  :title="$t('ReviseHeadImage')"
                  :list="[$t('MobilePhonePhoto'), $t('LocalPhoto')]"
                  :cancel="$tc('item',1)"
                  color="#575757"
                  @sheetHandle="sheetHandle"></action-sheet>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import messageBox from '../../components/MessageBox/index'
import actionSheet from '../../components/ActionSheet/index'
export default {
  name: 'personal',
  data () {
    return {
      bindType: 'unbind-wechat'
    }
  },
  computed: {
    ...mapGetters([
      'userInfo', 'isLogin', 'bindPhone', 'bindAlipay', 'bindWechat'
    ])
  },
  mounted () {
    this.dataInteractionResponse(data => {
      switch (data.msgType) {
        case '0x5122':
          if (data.result && data.data.logout) {
            this.setLogin(false)
            this.sidebarSwitch(false)
            this.$store.state.user.bindAlipay = false
            this.$store.state.user.bindWechat = false
            this.$store.state.user.bindPhone = false
            this.$router.replace('/login')
          }
          break
        case '0x5155':
          // 成功发送
          if (data.result) {
            this.loadingAlert(true)
          }
          break
        case '0x5167':
          if (data.result) {
            if (data.data.binding) {
              this.$store.state.user.bindAlipay = true
            }
          }
          break
        case '0x5165':
          if (data.result) {
            if (data.data.binding) {
              this.$store.state.user.bindWechat = true
            }
          }
          break
        case '0x516B':
          if (data.result) {
            if (data.data.unbind) {
              this.$store.state.user.bindWechat = false
            }
          }
          break
        case '0x516D':
          if (data.result) {
            if (data.data.unbind) {
              this.$store.state.user.bindAlipay = false
            }
          }
      }
    })
    if (this.ISDEBUG) {
      window.dataInteractionResponse('{"msgType":"0x5152","result":true,"data":{"nickName":"木心จุ๊บ","headPic":"http://thirdwxqlogocn/mmopen/vi_32/IBdic686IJ1d5PDK7fDWuCdRfGuBDoiasVWWSIHjTtBBibiaXWPYBjibsJfVSk9LCF0PmLLSR3f6h7sJibQeayVCCLMw/132","isHavePwd":false,"mobile":"13187855555","thirdInfos":[{"nickName":"木心จุ๊บ","type":10},{"nickName":"林林","type":11}]}}')
      window.dataInteractionResponse('{"msgType":"0x5155","result":true,"data":{"sent": true}}')
    }
  },
  components: {
    headTop,
    messageBox,
    actionSheet
  },
  methods: {
    ...mapActions([
      'setUserInfo', 'sidebarSwitch', 'setLogin', 'loadingAlert', 'setGlobalBox', 'setGlobalSheet'
    ]),
    itemHandle (num) {
      switch (num) {
        case 1:
          this.$router.push('/rename')
          break
        case 2:
          if (this.bindPhone) {
            this.bindType = 'unbind-phone'
            this.$router.push({name: 'bindingPhone', params: {bindType: 'unbind'}})
          } else {
            this.$router.push({name: 'bindingPhone', params: {bindType: 'bind'}})
          }
          break
        case 3:
          if (this.bindWechat) {
            this.bindType = 'unbind-wechat'
            this.setGlobalBox({bol: true, id: 7})
          } else {
            this.dataInteractionRequest({
              msgType: '0x5164'
            })
          }
          break
        case 4:
          if (this.bindAlipay) {
            this.bindType = 'unbind-alipay'
            this.setGlobalBox({bol: true, id: 7})
          } else {
            this.dataInteractionRequest({
              msgType: '0x5166'
            })
          }
          break
        default:
          break
      }
    },
    // 弹框
    boxHandle (confirm, cancel, id) {
      switch (id) {
        case 6:
          if (confirm) {
            // 用户登出请求
            this.dataInteractionRequest({
              msgType: '0x5121'
            })
            if (this.ISDEBUG) {
              let str = '{"msgType":"0x5122","result":true,"data":{"logout":true}}'
              window.dataInteractionResponse(str)
            }
          }
          break
        case 7:
          if (this.bindType === 'unbind-wechat') {
            this.dataInteractionRequest({
              msgType: '0x516A'
            })
          } else if (this.bindType === 'unbind-alipay') {
            this.dataInteractionRequest({
              msgType: '0x516C'
            })
          }
          break
      }
      this.setGlobalBox({bol: false, id: 0})
    },
    // 操作表
    openAvatar () {
      this.setGlobalSheet({bol: true, id: 2})
    },
    sheetHandle (confirm, value, id) {
      switch (id) {
        case 2:
          if (confirm) {
            if (value === 1) {
              // 手机拍照请求
              this.dataInteractionRequest({
                msgType: '0x5153'
              })
            } else {
              // 打开本地照片请求
              this.dataInteractionRequest({
                msgType: '0x5154'
              })
            }
          }
          break
      }
      this.setGlobalSheet({bol: false, id: 0})
    },
    logout () {
      this.setGlobalBox({bol: true, id: 6})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.personal{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    background: #f7f7f7;
    font-size: 26px;
    .avatar{
      .wh(100%, 300px);
      background: #FFF;
      display: flex;
      flex-direction: column;
      padding: 40px 20px 0 20px;
      .top{
        flex: 1;
        font-size: 40px;
        text-align: left;
        display: flex;
        align-items: center;
      }
      .bottom{
        flex: 4;
        display: flex;
        align-items: center;
        span{
          .wh(110px, 110px);
          border-radius: 50%;
          margin: 0 20px 0 10px;
          background-size: cover;
        }
        em{
          font-size: 46px;
        }
      }
    }
    .warp{
      position: absolute;
      left: 0;
      top: 327px;
      right: 0;
      bottom: 0;
      background: #FFF;
      .list{
        .wh(100%, 55%);
        display: flex;
        flex-direction: column;
        li{
          flex: 1;
          display: flex;
          align-items: center;
          padding: 0 40px;
          font-size: 32px;
          border-bottom: 0.5px solid #e6e4e4;
          span{
            flex: 1;
            text-align: left;
          }
          strong{
            flex: 1;
            text-align: right;
            padding-right: 40px;
            position: relative;
            color: #B0B0B0;
            &::after{
              content: '';
              display: block;
              .wh(28px, 28px);
              background: url('../../assets/sidebar_images/next@3x.png') 10px 0 no-repeat;
              background-size: contain;
              position: absolute;
              top: 50%;
              right: 0;
              transform: translateY(-50%);
            }
          }
          em{
            flex: 1;
            text-align: right;
            color: #B0B0B0;
          }
        }
      }
      .button{
        .wh-l(100%, 120px);
        color: #A2A2A2;
        font-size: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #F8F8F8;
        position: absolute;
        left: 0;
        bottom: 0;
        img{
          height: 40px;
          margin-right: 14px;
        }
      }
    }
  }
}
</style>



